* {
    margin: 0;
    padding: 0;
}
@font-face {
    font-family: 'Roboto';
    src: url('../fonts/Roboto-Black.ttf');
}
@font-face {
    font-family: 'Roboto-light';
    src: url('../fonts/Roboto-Light.ttf');
}
@font-face {
    font-family: 'Roboto-normal';
    src: url('../fonts/Roboto-Regular.ttf');
}

body {
    width: 100vw;
    height: max-content;
    display: flex;
    flex-direction: column;
    font-family: 'Roboto';
    font-size: 5em;
}
